let rulesList={};
_(_).keys().filter(i=>/^is/.test(i)).forEach(i=>rulesList[i]=_[i]);

function validator(target,rule){
	for(let key in rule){
		switch (typeof rule[key]){
			case 'boolean':{
				if(rule[key]===true){
					if(_.isFunction(rulesList[key])){
						if(!rulesList[key](target)){
							return false;
						}
					}else{
						throw (`${key}`);
					}
				}
				break;
			}
			case 'string':{
				if(_.isFunction(rulesList[key])){
					if(!rulesList[key](target)){
						return false;
					}
				}else{
					throw (`${key}`);
				}
			}

		}
	}
	return true;
}
export default validator;